@font-face {
  font-family: 'Ownglyph_UNZ-Rg';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2406-1@1.0/Ownglyph_UNZ-Rg.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}


*{
  box-sizing: border-box;
  font-family: 'Ownglyph_UNZ-Rg';
}

body{
  margin: 0;
  padding: 0;
}

button, a{
  all: unset;
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6, p{
  margin: 0;
  padding: 0;
}